<template>
  <table class="table" :class="tableClass">
    <thead>
    <slot name="columns">
      <th v-for="column in columns" :key="column">{{column}}</th>
    </slot>
    </thead>
    <tbody>
    <tr v-for="(item, index) in data" :key="index">
      <slot :row="item">
        <td v-for="(column, index) in columns"
            :key="index"
           >
          {{itemValue(item, column)}}
        </td>
        <td>
          <i style="cursor:pointer" @click="editItem(item)" class="ti-settings"></i>
        </td>
      </slot>
    </tr>
    </tbody>
  </table>
</template>
<script>
import bus from '../eventBus/eventBus.js'
export default {
  name: 'paper-table',
  props: {
    columns: Array,
    data: Array,
    type: {
      type: String, // striped | hover
      default: "striped"
    },
    title: {
      type: String,
      default: ""
    },
    subTitle: {
      type: String,
      default: ""
    }
  },
  computed: {
    tableClass() {
      return `table-${this.type}`;
    }
  },
  methods: {
    hasValue(item, column) {
      return item[column] !== "undefined";
    },
    itemValue(item, column) {
      return item[column];
    },
    editItem(p) {
      bus.$emit('editItem', p)
    }
  }
};
</script>
<style>
</style>



