<template>
<div class="row">
  <div class="col-12">
  <card class="card" :title="'Formulaire ' + this.entityName">
      <form @submit.prevent>
      
        <div class="row">
          <div v-for="text in form" :key='text' class="col-6">
            <fg-input type="text"
                      :label="text"
                      :placeholder="text"
                      v-model="newEntity[text]">
            </fg-input>
          </div>
         </div>
          <div class="text-center">
          <p-button type="info"
                    round
                    @click.native.prevent="postNewEntity">
            Enregistrer
          </p-button>
        </div>
         
        <div class="clearfix"></div>
        
      </form>
        </card>
         <b-modal size="lg" ref="myModalRef" hide-footer hide-header>
      <invoice id="downloadPdf" :invoice='dataEntityEdit'></invoice>
      <b-btn class="mt-3" variant="outline-success" block @click="downloadPdf">Télécharger</b-btn>
      <b-btn class="mt-3" variant="outline-danger" block @click="deleteEntity">Supprimer {{entityName}}</b-btn>
    </b-modal>
        <card  :title="'Liste ' + entityName" :subTitle="'Permet de consulter les informations des ' + this.entityModel.name">
          <div slot="raw-content" class="table-responsive">
            <paper-table :data="dataEntity" :columns="this.tableColumns">
            </paper-table>
          </div>
        </card>
  </div>

      </div>


 
</template>
<script>
import { PaperTable } from "@/components";
import lodash from "lodash";
import Swal from "sweetalert2";
import html2canvas from "html2canvas";
import jsPDF from "jspdf";
import $ from "jquery";
import Invoice from "./Invoice.vue";
import bus from "../eventBus/eventBus.js";
export default {
  props: ["entityModel"],
  data() {
    return {
      entityName: this.entityModel.name,
      url: {
        post: this.entityModel.url.post,
        edit: this.entityModel.url.edit
      },
      form: this.entityModel.form,
      dataEntity: [],
      tableColumns: this.entityModel.arrayTableHeaders,
      dataEntityEdit: {},
      newEntity: this.entityModel.dataModel
    };
  },
  components: {
    PaperTable,
    Invoice
  },
  mounted() {
    this.getDataEntity();
    this.updateDataEntity();
  },
  methods: {
    getDataEntity() {
      this.dataEntity = [];
      this.$http.get(this.url.post).then(res => this.parseDataEntity(res.body));
    },
    showModal() {
      this.$refs.myModalRef.show();
    },
    hideModal() {
      this.$refs.myModalRef.hide();
    },
    parseDataEntity(a) {
      if (a !== null) {
        Object.keys(a).forEach(k => {
          a[k].key = k;
          this.dataEntity.push(a[k]);
          console.log(this.dataEntity);
        });
      }
    },
    postNewEntity() {
      for (let val in this.newEntity) {
        if (this.newEntity[val] === "") {
          return this.$toasted.show(
            "Merci de renseigner l'ensemble des champs requis."
          );
        }
      }
      this.$http.post(this.url.post, this.newEntity).then(res => {
        if (res.status === 200) {
          this.$toasted.show(this.entityName + " enregistré avec succés.");
          this.refreshForm();
          this.getDataEntity();
        }
      });
    },
    deleteEntity() {
      Swal({
        text: this.entityName + " n'apparaira plus dans la liste",
        type: "warning",
        showCancelButton: true,
        confirmButtonText: "Valider",
        cancelButtonText: "Annuler"
      }).then(result => {
        if (result.value) {
          this.$http
            .delete(this.url.edit + this.dataEntityEdit.key + ".json")
            .then(res => {
              Swal(
                "Suppression réussie",
                this.entityName + " à été retiré de la liste",
                "success"
              );
              this.dataEntity = [];
              this.hideModal();
              this.getDataEntity();
            });
        } else if (result.dismiss === Swal.DismissReason.cancel) {
          return;
        }
      });
    },
    putDataEntity() {
      this.$http
        .put(
          this.url.edit + this.dataEntityEdit.key + ".json",
          this.dataEntityEdit
        )
        .then(res => {
          this.$toasted.show(this.entityName + " modifié avec succés.");
          this.dataEntityEdit = [];
          this.getDataEntity();
        });
    },
    updateDataEntity() {
      bus.$on("editItem", p => {
        this.dataEntityEdit = { ...p };
        this.showModal();
        this.$toasted.show("Édition de " + this.entityName + " en cours.");
      });
    },
    refreshForm() {
      this.newEntity = { ...this.entityModel.dataModel };
    },
    downloadPdf() {
      html2canvas(document.querySelector("#downloadPdf")).then(canvas => {
        var a = document.createElement("a");
        a.href = canvas
          .toDataURL("image/jpeg")
          .replace("image/jpeg", "image/octet-stream");
        a.download = "Facture.jpg";
        a.click();
      });
    }
  }
};
</script>
<style>
</style>



