<template>
<div class="row">
  <div class="col-12">
  <card class="card" title="Formulaire client">
      <form @submit.prevent>
      
        <div class="row">
          <div class="col-md-6">
            <fg-input type="text"
                      label="Nom"
                      placeholder="Nom du client"
                      v-model="user.name">
            </fg-input>
          </div>
          <div class="col-md-6">
            <fg-input type="text"
                      label="Prénom"
                      placeholder="Prénom du client"
                      v-model="user.firstname">
            </fg-input>
          </div>
        </div>


         <div class="col-md-12">
            <fg-input type="email"
                      label="Adresse email"
                      placeholder="Adresse email du client"
                      v-model="user.email">
            </fg-input>
          </div>

             <div class="col-md-12">
            <fg-input type="telephone"
                      label="Numéro de téléphone"
                      placeholder="Numéro de téléphone du client"
                      v-model="user.telephone">
            </fg-input>
          </div>

          <div class="col-md-12">
            <fg-input type="text"
                      label="Adresse"
                      placeholder="Domicile du client"
                      v-model="user.address">
            </fg-input>
          </div>

        <div class="row">
          <div class="col-md-4">
            <fg-input type="text"
                      label="Ville"
                      placeholder="Ville du client"
                      v-model="user.city">
            </fg-input>
          </div>
          <div class="col-md-4">
            <fg-input type="text"
                      label="Pays"
                      placeholder="Pays du client"
                      v-model="user.country">
            </fg-input>
          </div>
          <div class="col-md-4">
            <fg-input type="number"
                      label="Code postal"
                      placeholder="Code postal du client"
                      v-model="user.postalcode">
            </fg-input>
          </div>
        </div>

        <div class="row">
          <div class="col-md-12">
            <div class="form-group">
              <label>Détails & particularités</label>
              <textarea rows="5" class="form-control border-input"
                        placeholder="Décrivez les détails et particularités du client face au tatouages"
                        v-model="user.detail">

              </textarea>
            </div>
          </div>
        </div>
        <div class="text-center">
          <p-button type="info"
                    round
                    @click.native.prevent="updateProfile">
            Enregistrer le client
          </p-button>
        </div>
        <div class="clearfix"></div>
      </form>
        </card>

         <div>
    <b-modal  ref="myModalRef" hide-footer :title="'Fiche client : ' + this.editUser.name + ' ' + this.editUser.firstname">
      <form @submit.prevent>
      
        <div class="row">
          <div class="col-md-6">
            <fg-input type="text"
                      label="Nom"
                      placeholder="Nom du client"
                      v-model="editUser.name">
            </fg-input>
          </div>
          <div class="col-md-6">
            <fg-input type="text"
                      label="Prénom"
                      placeholder="Prénom du client"
                      v-model="editUser.firstname">
            </fg-input>
          </div>
        </div>


         <div class="col-md-12">
            <fg-input type="email"
                      label="Adresse email"
                      placeholder="Adresse email du client"
                      v-model="editUser.email">
            </fg-input>
          </div>

             <div class="col-md-12">
            <fg-input type="telephone"
                      label="Numéro de téléphone"
                      placeholder="Numéro de téléphone du client"
                      v-model="editUser.telephone">
            </fg-input>
          </div>

          <div class="col-md-12">
            <fg-input type="text"
                      label="Adresse"
                      placeholder="Domicile du client"
                      v-model="editUser.address">
            </fg-input>
          </div>

        <div class="row">
          <div class="col-md-4">
            <fg-input type="text"
                      label="Ville"
                      placeholder="Ville du client"
                      v-model="editUser.city">
            </fg-input>
          </div>
          <div class="col-md-4">
            <fg-input type="text"
                      label="Pays"
                      placeholder="Pays du client"
                      v-model="editUser.country">
            </fg-input>
          </div>
          <div class="col-md-4">
            <fg-input type="number"
                      label="Code postal"
                      placeholder="Code postal du client"
                      v-model="editUser.postalcode">
            </fg-input>
          </div>
        </div>

        <div class="row">
          <div class="col-md-12">
            <div class="form-group">
              <label>Détails & particularités</label>
              <textarea rows="5" class="form-control border-input"
                        placeholder="Décrivez les détails et particularités du client face au tatouages"
                        v-model="editUser.detail">

              </textarea>
            </div>
          </div>
        </div>
        <div class="clearfix"></div>
      </form>
      <b-btn class="mt-3" variant="outline-info" block @click="putUser">Enrgistrer les modifications</b-btn>
      <b-btn class="mt-3" variant="outline-danger" block @click="deleteUser">Supprimer le client</b-btn>
      <b-btn class="mt-3" variant="outline-warning" block @click="hideModal">Fermer</b-btn>
    </b-modal>
  </div>

      
          
        <card  :title="'Liste des clients'" :subTitle="'Permet de consulter les informations des clients'">
          <div slot="raw-content" class="table-responsive">
            <paper-table :data="users" :columns="this.tableColumns">
            </paper-table>
          </div>
        </card>
  </div>
      </div>


 
</template>
<script>
import { PaperTable } from "@/components";
import lodash from "lodash";
import Swal from "sweetalert2";
import bus from "../../eventBus/eventBus.js";
export default {
  data() {
    return {
      url: {
        post: "https://oldschooltatoo-2aa33.firebaseio.com/users.json"
      },
      users: [],
      tableColumns: [
        "name",
        "firstname",
        "city",
        "postalcode",
        "country",
        "telephone",
        "detail",
        "email"
      ],
      editUser: {},
      user: {
        name: "",
        firstname: "",
        city: "",
        postalcode: "",
        country: "",
        telephone: "",
        detail: "",
        email: ""
      }
    };
  },
  components: {
    PaperTable
  },
  mounted() {
    this.getUsers();
    this.updateUser();
  },
  methods: {
    getUsers() {
      this.users = [] 
      this.$http.get(this.url.post).then(res => this.parseUsers(res.body));
    },
    showModal() {
      this.$refs.myModalRef.show();
    },
    hideModal() {
      this.$refs.myModalRef.hide();
    },
    parseUsers(a) {
      Object.keys(a).forEach(k => {
        a[k].key = k;
        this.users.push(a[k]);
      });
    },
    updateProfile() {
      for(let val in this.user) {
        if(this.user[val] === '') return this.$toasted.show(
            "Merci de renseigner l'ensemble des champs requis."
          );
      }
      this.$http.post(this.url.post, this.user).then(res => {
        if (res.status === 200) {
          this.$toasted.show(
            "Le client " + this.user.name + " à été enregistré avec succés."
          );
          this.refreshForm();
        }
      });
    },
     deleteUser() {
      Swal({
        text:
          "le client " + this.editUser.name + " n'apparaira plus dans la liste",
        type: "warning",
        showCancelButton: true,
        confirmButtonText: "Valider",
        cancelButtonText: "Annuler"
      }).then(result => {
        if (result.value) {
          this.$http
            .delete(
              "https://oldschooltatoo-2aa33.firebaseio.com/users/" +
                this.editUser.key +
                ".json"
            )
            .then(res => {
              Swal(
                "Suppression réussie",
                "Le produit " + this.editUser.name + " à été retiré de la liste",
                "success"
              );
              this.users = []
              this.hideModal()
              this.getUsers();
            });
        } else if (result.dismiss === Swal.DismissReason.cancel) {
          return;
        }
      });
    },
    putUser() {
      this.$http
        .put(
          "https://oldschooltatoo-2aa33.firebaseio.com/users/" +
            this.editUser.key +
            ".json", this.editUser
        )
        .then(res => {
          this.$toasted.show(
            "Client " +
              this.editUser.name +
              " " +
              this.editUser.firstname +
              " modifié avec succés."
          );
          this.users = []
          this.getUsers();
        });
    },
    updateUser() {
      bus.$on("editItem", p => {
        this.editUser = { ...p };
        this.showModal();
        this.$toasted.show(
          "Édition de " +
            this.editUser.name +
            " " +
            this.editUser.firstname +
            " en cours."
        );
      });
    },
    refreshForm() {
      this.user = {
        name: "",
        firstname: "",
        city: "",
        postalcode: "",
        country: "",
        telephone: "",
        detail: "",
        email: ""
      };
    }
  }
};
</script>
<style>
</style>



