<template>
    <div class="row">
      <div class="col-12">
        <card :title="formTitle" :subTitle="'Merci de renseigner tout les champs nécessaires'">
       <form @submit.prevent>
        <div class="row">
          <div class="col-md-5">
            <fg-input type="text"
                      label="Nom du produit"
                      placeholder="Exemple: encre noir"
                       v-model="product.name"
                      >
            </fg-input>
          </div>
          <div class="col-md-3">

            <fg-input type="number"
                      label="Quantité initiale"
                      v-model="product.quantity"
                      >
            </fg-input>
          </div>
          <div class="col-md-4">
    <div class="form-group">
    <label for="exampleSelect1">Catégorie</label>
    <select class="form-control"  v-model="product.category" id="exampleSelect1">
      <option value="Encres">Encres</option>
      <option value="Fournitures">Fournitures</option>
    </select>
  </div>
          </div>
        </div>

        <div class="text-center">
          <p-button type="info"
                    round
                    v-if="showAddButton"
                    @click.native.prevent="newProduct">
            Ajouter
          </p-button>
           <p-button type="success"
                    round
                     v-if="showEditButton"
                    @click.native.prevent="editItem">
            Enrgistrer les modifications
          </p-button>
           <p-button type="danger"
                    round
                     v-if="showDeleteButton"
                    @click.native.prevent="deleteItem">
            Supprimer
          </p-button>
        <div class="clearfix"></div>
           <p-button type="info"
                    round
                     v-if="showCancelButton"
                    @click.native.prevent="cancelEdit">
            Annuler les modifications
          </p-button>
        </div>
        <div class="clearfix"></div>
       
      </form>
       </card>
      </div>
     
      <div class="col-md-6 col-sm-12">
        <card :title="table1.title" :subTitle="table1.subTitle">
          <div slot="raw-content" class="table-responsive">
            <paper-table :data="table1.data" :columns="table1.columns">

            </paper-table>
          </div>
        </card>
      </div>


      <div class="col-md-6 col-sm-12">
        <card :title="table2.title" :subTitle="table2.subTitle">
          <div slot="raw-content" class="table-responsive">
            <paper-table :data="table2.data" :columns="table1.columns">

            </paper-table>
          </div>
        </card>
      </div>

    </div>
</template>
<script>
import { PaperTable } from "@/components";
import lodash from "lodash";
import Swal from "sweetalert2";
import bus from "../eventBus/eventBus.js";
const tableColumns = ["name", "quantity", "category"];
export default {
  components: {
    PaperTable
  },
  data() {
    return {
      formTitle: "Nouveau produit",
      showEditButton: false,
      showDeleteButton: false,
      showAddButton: true,
      showCancelButton: false,
      url: {
        post: "https://oldschooltatoo-2aa33.firebaseio.com/products.json"
      },
      product: {
        name: "",
        quantity: 0,
        category: ""
      },
      table1: {
        title: "Liste des encres",
        subTitle: "Consulter, editer, ou supprimer un produit dans la liste.",
        columns: [...tableColumns],
        data: []
      },
      table2: {
        title: "Liste des fournitures",
        subTitle: "Consulter, editer, ou supprimer un produit dans la liste.",
        columns: [...tableColumns],
        data: []
      }
    };
  },
  mounted() {
    this.getAllProducts();
    this.listenForEdit();
  },
  methods: {
    newProduct() {
      if (this.product.name === "" || this.product.category === "")
        return this.$toasted.show(
          "Merci de renseigner l'ensemble des champs pour ajouter un produit."
        );
      this.$http.post(this.url.post, this.product).then(res => {
        this.$toasted.show(
          "Le produit " + this.product.name + " à été enregistré avec succés."
        );
        this.getAllProducts();
        this.refreshDataObj();
      });
    },
    getAllProducts() {
      this.$http.get(this.url.post).then(res => this.mapProduct(res.body));
    },
    mapProduct(a) {
      let product = [];
      Object.keys(a).forEach(key => {
        a[key].id = key;
        product.push(a[key]);
      });
      this.table1.data = lodash.filter([...product], { category: "Encres" });
      this.table2.data = lodash.filter([...product], {
        category: "Fournitures"
      });
    },
    listenForEdit() {
      bus.$on("editItem", p => {
        this.product = p;
        this.formTitle = "Modification produit";
        this.activateForm(true, true, false, true);
      });
    },
    deleteItem() {
      Swal({
        text:
          "le produit " + this.product.name + "n'apparaira plus dans la liste",
        type: "warning",
        showCancelButton: true,
        confirmButtonText: "Valider",
        cancelButtonText: "Annuler"
      }).then(result => {
        if (result.value) {
          this.$http
            .delete(
              "https://oldschooltatoo-2aa33.firebaseio.com/products/" +
                this.product.id +
                ".json"
            )
            .then(res => {
              Swal(
                "Suppression réussie",
                "Le produit " + this.product.name + " à été retiré de la liste",
                "success"
              );
              this.refreshDataObj();
              this.activateForm(false, false, true, false);
              this.formTitle = "Nouveau produit";
              this.showEditButton = false;
              this.getAllProducts();
            });
        } else if (result.dismiss === Swal.DismissReason.cancel) {
          return;
        }
      });
    },
    editItem() {
      this.$http.put("https://oldschooltatoo-2aa33.firebaseio.com/products/" +
                this.product.id +
                ".json", this.product
            ).then(res => {
              this.getAllProducts()
              this.$toasted.show(
          "Le produit " + this.product.name +" à été modifié avec succés."
        );
        this.refreshDataObj()
        this.activateForm(false, false, true, false)
        this.formTitle = 'Nouveau produit'
            })
    },
    activateForm(p1, p2, p3, p4) {
      this.showEditButton = p1;
      this.showDeleteButton = p2;
      this.showAddButton = p3;
      this.showCancelButton = p4
    },
    refreshDataObj() {
      this.product = {
        name: "",
        quantity: 0,
        category: "",
        id: ""
      };
    },
    cancelEdit() {
       this.refreshDataObj()
       this.activateForm(false, false, true, false)
       this.formTitle = 'Nouveau produit'
    }
  }
};
</script>
<style>
.btn:not(:disabled):not(.disabled) {
    cursor: pointer;
    margin: 5px;
}
</style>



